<template>
  <div>
    <div class="d-flex flex-column">
      <span
        >Introduce el código de verificación que has recibido en tu correo
        electrónico:</span
      >
      <form class="form-wrapper mt-4 mb-4" @submit.prevent>
        <label>Código de verificación</label>
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <small
              class="float-right label-remember-pass cpointer"
              v-bind="attrs"
              v-on="on"
              >¿No has recibido ningún código?</small
            >
          </template>
          <span>Infórmanos del error al siguiente correo: <b>agf.smartdesign@gmail.com</b></span>
        </v-tooltip>

        <div class="d-flex align-center ">
          <v-text-field
            v-model="code"
            class="pt-1"
            :error-messages="codeErrors"
            required
            single-line
            outlined
            dense
            placeholder="Introduce el código de verificación"
            :filled="!codeIsTouched"
            :append-icon="isValidCode !== null ? (isValidCode ? 'mdi-check' : 'mdi-close') : ''"
            color="success"
            @input="$v.code.$touch();checkCode()"
            @focus="codeIsTouched = true"
            @blur="
              $v.code.$touch();
              codeIsTouched = false;
            "
          ></v-text-field>
        </div>
      </form>
    </div>
    <div class="d-flex justify-end">
      <v-tooltip bottom>
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            icon
            color="grey"
            v-bind="attrs"
            v-on="on"
            text
            @click="goPreviusStep()"
          >
            <v-icon large>mdi-arrow-left-circle</v-icon>
          </v-btn>
        </template>
        <span>Atrás</span>
      </v-tooltip>
      <v-tooltip bottom>
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            icon
            color="success"
            v-bind="attrs"
            v-on="on"
            text
            :disabled="!isValidCode"
            @click="goNextStep()"
          >
            <v-icon large>mdi-arrow-right-circle</v-icon>
          </v-btn>
        </template>
        <span>Siguiente</span>
      </v-tooltip>
    </div>
  </div>
</template>
<script>
import { validationMixin } from "vuelidate";
import { required } from "vuelidate/lib/validators";
// services
import ServicesRegister from "./../../../services/register/services";
import ServicesLogin from "./../../../services/login/services";
export default {
  name: "CodeValidation",
  mixins: [validationMixin],
  validations: {
    code: { required },
  },

  data: () => ({
    code: "",
    codeIsTouched: null,
    isValidCode: null
  }),

  computed: {
    codeErrors() {
      const errors = [];
      if (!this.$v.code.$dirty) return errors;
      !this.$v.code.required && errors.push("El código es requerido");
      return errors;
    },
  },

  methods: {
    checkCode() {
      this.$v.$touch();
      this.isValidCode = null;
      if (this.code && this.code.length === 6 && !this.$v.$invalid) {
        let payload = {
          code: parseInt(this.code),
        };
        ServicesLogin.checkCodeIsValid(payload).then((response) => {
          if (response.data.code === 200) {
            this.isValidCode = true;
          } else {
            this.isValidCode = false;
          }
        });
      }
    },
    goNextStep() {
      this.$emit("goStep", "+");
    },
    goPreviusStep() {
      this.$emit("goStep", "-");
    },
  },
};
</script>
<style lang="scss">
.label-remember-pass:hover {
  text-decoration: underline;
}
</style>
