<template>
  <v-stepper v-model="currentStep">
    <v-stepper-header>
      <v-stepper-step color="success" :complete="currentStep > 1" step="1">
        Validar correo
      </v-stepper-step>

      <v-divider></v-divider>

      <v-stepper-step color="success" :complete="currentStep > 2" step="2">
        Validar código
      </v-stepper-step>

      <v-divider></v-divider>

      <v-stepper-step color="success" step="3">
        Crear nueva contraseña
      </v-stepper-step>
    </v-stepper-header>

    <v-stepper-items>
      <v-stepper-content step="1">
        <MailValidationComponent @goStep="moveBySteps"/>
      </v-stepper-content>

      <v-stepper-content step="2">
        <CodeValidationComponent @goStep="moveBySteps"/>
      </v-stepper-content>

      <v-stepper-content step="3">
        <v-card class="mb-12" color="grey lighten-1" height="200px"></v-card>

        <v-btn color="primary" @click="currentStep = 1">
          Continue
        </v-btn>

        <v-btn text>
          Cancel
        </v-btn>
      </v-stepper-content>
    </v-stepper-items>
  </v-stepper>
</template>
<script>
// components
import MailValidationComponent from './recoverPasswordStepper/MailValidationComponent';
import CodeValidationComponent from './recoverPasswordStepper/CodeValidationComponent';
export default {
  name: "RecoverPasswordModal",
  components:{
    MailValidationComponent,
    CodeValidationComponent
  },
  data: () => ({
    currentStep: 1,
  }),

  methods: {
    // emit events
    moveBySteps(sign){
      if(sign === '+'){
        this.currentStep++;
      } else {
        this.currentStep--;
      }
    }
  },
};
</script>
<style lang="scss" scoped>
.form-wrapper {
  width: 100%;
}
.v-stepper{
  box-shadow: none;
  border-radius: 0px;
}
.v-stepper__header{
  box-shadow: none;
  background-color: #efefef;
  border-radius: 0px;
}
</style>
